@Component({
  selector: 'page-historical',
  animations: [
    trigger(
      'myAnimation', [
        transition(
          ':enter', [
            style({
              transform: 'translateY(100%)',
              opacity: 0
            }),
            animate('200ms', style({
              transform: 'translateY(0)',
              'opacity': 1
            }))
          ]
        ),
        transition(
          ':leave', [
            style({
              transform: 'translateY(0)',
              'opacity': 1
            }),
            animate('400ms', style({
              transform: 'translateY(100%)',
              'opacity': 0
            }))

          ]
        )
      ]
    )
  ],
  templateUrl: 'historical.html'
})

export class Historical {
  @ViewChild('mycontent') nav: NavController

  @ViewChild('myGauge') myGauge: jqxGaugeComponent;
  @ViewChild('gaugeValue') gaugeValue: ElementRef;
  // ngAfterViewInit(): void {
  //   this.myGauge.value(0);   
  //  }
  //labels: any = { position: 'outside', interval: 20 };
  labels: any = {
    position: 'outside',
    interval: 20,
    formatValue: (value: number, position: string): string => {
      if (value < 181)
        return value + '';
    }
  };
  ticksMinor: any = {
    interval: 5,
    size: '5%'
  };

  ticksMajor: any = {
    interval: 10,
    size: '10%'
  };

  cap: any = {
    size: '5%',
    style: {
      fill: '#2e79bb',
      stroke: '#2e79bb'
    }
  };

  border: any = {
    style: {
      fill: '#8e9495',
      stroke: '#7b8384',
      'stroke-width': 1
    }
  };

  pointer: any = {
    style: {
      fill: '#2e79bb'
    },
    width: 5
  };

  ranges: any[] = [{
      startValue: 0,
      endValue: 60,
      style: {
        fill: '#4cb848',
        stroke: '#4cb848'
      },
      startDistance: 0,
      endDistance: 0
    },
    {
      startValue: 60,
      endValue: 100,
      style: {
        fill: '#fad00b',
        stroke: '#fad00b'
      },
      startDistance: 0,
      endDistance: 0
    },
    {
      startValue: 100,
      endValue: 180,
      style: {
        fill: '#e53d37',
        stroke: '#e53d37'
      },
      startDistance: 0,
      endDistance: 0
    }
  ];
  dash = -30;
  setShowDashbord() {
    if (this.dash == -30)
      this.dash = -140;
    else
      this.dash = -30;
  }
  getShowDashbord() {
    return this.dash;
  }
  show: boolean = false;
  vehicule: any;
  groupmodal: any;
  recordtime: any;
  matricule: any = "Temps Réel";
  realtimecoord: any;
  mileageClicked: Mileage;
  stopClicked: Stop;
  pois: Poi[];
  stops: Stop[] = [];
  firstDate: any;
  lastDate: any;
  pointInterests: PointInterest[] = null;
  groups: Group[];
  allRealTimeRecords: Subscription;
  allGroups: Subscription;
  allPaths: Subscription;
  paths: any[];
  loader: any;
  selectedDevice: any;
  oldRealTimeRecords: RealTimeRecord[] = null;
  angles: Map < number, number > = new Map();
  pathDrawn: boolean = false;
  previousPathdrawn: boolean = false;
  pathDraw: boolean = false;
  currentPathClickedDeviceIds: number[] = null;
  modalopen: boolean = false;
  dismissbutton: boolean = false;
  driverphone: string;
  poisInMap: boolean = false;
  latpoi: any;
  lngpoi: any;
  addresspoi: any;
  currentlocationlat: any;
  currentlocationlng: any;
  resultopened: boolean = false;
  openStopResult: boolean = false;
  openMileageResult: boolean = false;
  AllPaths: any;
  mileages: Mileage[] = [];
  totalMileage: Mileage;
  pathpoirealtime: boolean = false;
  Root: boolean;
  relativeposition: string;
  vitesse: number;
  imageUri: string = "../../assets/icons/c1x0.png";
  history:boolean=false;
  constructor(public plm: Platform, public events: Events, private menu: MenuController, statusBar: StatusBar, private alertCtrl: AlertController, private launchNavigator: LaunchNavigator, private geolocation: Geolocation, public actionSheetCtrl: ActionSheetController, public viewCtrl: ViewController, private callNumber: CallNumber, private storage: Storage, private pipe: DecimalPipe, public toastController: ToastController, private realTimeService: RealTimeService, private geocodingService: GeocodingService, private dataManagementService: DataManagementService, public _app: App, private historicalService: HistoricalService, public loadingCtrl: LoadingController, public modalCtrl: ModalController, public navCtrl: NavController, public navParams: NavParams, public mapService: MapService) {
    this.navCtrl.swipeBackEnabled = false;
    this.totalMileage = new Mileage();
    this.Root = JSON.parse(localStorage.getItem('currentuser')).isRoot;
    this.geolocation.getCurrentPosition().then((resp) => {
      this.currentlocationlat = resp.coords.latitude;
      this.currentlocationlng = resp.coords.longitude;
    }).catch((error) => {
      console.log('Error getting location', error);
    });
    events.subscribe('Showinfo', x => {
      if (x) {
        this.show = !this.show;
      } else this.show = false;
    });
  }

  // initialisation du map +open group modal
  ngOnInit() {

    this.initMap();
    this.loader = this.loadingCtrl.create({
      content: "Veuillez attendre..."
    });
    this.loader.present();
    this.openGroupsModal();
    this.loader.dismiss();
    console.log(this.history)
    
    setInterval(() => {
      if(!this.history)
      this.reCalculeRealTimeRecord();
    }, 20000);
  }
  reCalculeRealTimeRecord() {
    if (this.vehicule) {
      this.vehicule.realTimeRecord = new RealTimeRecord();
      this.realTimeService.getRealTimeRecord(this.selectedDevice).subscribe(y => {
        this.vehicule.realTimeRecord = y;
        this.vehicule.realTimeRecord.vehicule = new RealTimeRecord();
        this.vehicule.realTimeRecord.vehicule = this.vehicule;
        this.trackRealTimeRecord(this.vehicule.realTimeRecord);
      });
    }
  }
  // open menu
  openmenu() {
    this.menu.open();
  }

  openmodalrealtime() {
    this.show=false;
    this.history=false;

    this.resultopened = false;
    this.openStopResult = false;
    this.openMileageResult = false;
    this.previousPathdrawn = false;
    this.pathDrawn = false;
    this.clearPolylines();
    this.groupmodal.present();
  }



  //initialisation du map
  initMap() {
    if (this.mapService.map) this.mapService.map.remove();
    let map = L.map('historicalMap', {
      zoomControl: false,
      center: L.latLng(32.586163, -9.912118),
      zoom: 6,
      minZoom: 3,
      maxZoom: 20,
      maxNativeZoom: 17,
      layers: [this.mapService.baseMaps.googlestreet]
    });
    L.control.layers(this.mapService.baseMaps, null, {
      position: 'bottomright'
    }).addTo(map);
    L.control.zoom({
      position: 'topright'
    }).addTo(map);
    this.mapService.map = map;
  }

  //draw itineraire
  drawpath() {
    if (this.selectedDevice) {
      if (this.currentlocationlat && this.currentlocationlng) {
        let options: LaunchNavigatorOptions = {
          start: [this.currentlocationlat, this.currentlocationlng],
          app: this.launchNavigator.APP.GOOGLE_MAPS

        };
        this.launchNavigator.navigate([this.latpoi, this.lngpoi], options)
          .then(
            success => console.log('Launched navigator'),
            error => {
              let alert = this.alertCtrl.create({
                title: 'Attention',
                subTitle: 'Veuillez installer Google Maps ',
                buttons: ['Ok']
              });
              alert.present();
            }
          );
      } else {
        let alert = this.alertCtrl.create({
          title: 'Attention',
          subTitle: 'Veuillez activer GPS ',
          buttons: ['Ok']
        });
        alert.present();
      }
    } else {
      let alert = this.alertCtrl.create({
        title: 'Itinéraire',
        subTitle: 'Veuillez choisir un véhicule ',
        buttons: ['Ok']
      });
      alert.present();
    }

  }
  //open gestion de park
  openParc() {
    let data = {
      idDevice: this.vehicule,
      matricule: this.matricule
    }
    let gestparc = this.modalCtrl.create(GestionPark, {
      data
    });
    gestparc.present();


  }
  // (open modal) liste des groupes et véhicules (temps réel) 
  openGroupsModal() {
    this.pathpoirealtime = true;
    this.history=false;
    this.resultopened = false;
    this.openStopResult = false;
    this.openMileageResult = false;
    this.groupmodal = this.modalCtrl.create(GroupsPage);
    this.groupmodal.present();
    this.groupmodal.onDidDismiss(data => {
      if (data) {
        this.vehicule = data.vehicule;
        this.mapService.map.setView(data.vehicule.realTimeRecord.coordinate, 15);
        this.trackRealTimeRecord(data.vehicule.realTimeRecord);
      }
    });
    this.modalopen = false;
    this.dismissbutton = true;
  }

  //open POI Modal
  openPoiModal() {
    if (this.selectedDevice) {
      let pointInterest = new PointInterest();
      pointInterest.address = this.addresspoi;
      pointInterest.coordinate.lat = this.latpoi;
      pointInterest.coordinate.lng = this.lngpoi;
      let PoiModal = this.modalCtrl.create(Poi, {
        pointInterest
      });
      PoiModal.present();
      PoiModal.onDidDismiss(form => {
        if (form.pointinterest.name != null) {
          if (pointInterest.type == "MARKER") {
            let circle = L.circle(pointInterest.coordinate, {
              color: 'red',
              fillColor: '#f03',
              fillOpacity: 0.1,
              radius: pointInterest.ray
            });
            this.mapService.addCircle(circle);
            setTimeout(() => {
              this.mapService.removeCirclesFromMap();
            }, 4000);
          }


        }
      });
    } else {
      let alert = this.alertCtrl.create({
        title: 'POI',
        subTitle: 'Veuillez choisir un véhicule ',
        buttons: ['Ok']
      });
      alert.present();
    }
  }

  //remove polylines and markers from map
  clearPolylines() {
    this.mapService.removePolylinesFromMap();
    this.mapService.removeMarkersFromMap();
    this.pathDrawn = false;
  }

  //Getting old real time record
  getOldRealTimeRecord(id: number): RealTimeRecord {
    let result = this.oldRealTimeRecords.filter(rt => {
      return rt.idRealTimeRecord == id;
    });
    if (result) return result[0];
    else return null;
  }

  // Compare two coordinate
  compareTwoCoordinate(p1, p2) {
    if (p1.lat == p2.lat && p1.lng == p2.lng) return true;
    else false;
  }
  //
  getGeocoding(address: any): string {
    let geocoding: string = null;
    if (address) {
      if (address.road != null) {
        geocoding = address.road;
      }
      if (address.neighbourhood != null) {
        geocoding = geocoding ? geocoding + ' ' + address.neighbourhood : address.neighbourhood;
      }
      if (address.city != null) {
        geocoding = geocoding ? geocoding + ' (' + address.city + ')' : address.city;
      }
      if (geocoding == null) {
        geocoding = 'chargement..';
      }
    }
    return geocoding;
  }

  // get vehicule
  getVehicule(idDevice: number): Vehicule {
    let foundVehicule = new Vehicule();
    for (let i = 0; i < this.groups.length; i++) {
      for (let j = 0; j < this.groups[i].vehicules.length; j++) {
        if (this.groups[i].vehicules[j].idDevice == idDevice) {
          foundVehicule = this.groups[i].vehicules[j];
          break;
        }
      }
    }
    return foundVehicule;
  }


  //temps reel 
  trackRealTimeRecord(realTimeRecord: RealTimeRecord) {
    // this.myGauge.value(realTimeRecord.speed); 
    this.pathDrawn = false;
    let angle = 0;
    var date = new Date(realTimeRecord.recordTime);
    realTimeRecord.recordTime = new Date(new Date(realTimeRecord.recordTime).setHours(new Date(realTimeRecord.recordTime).getHours() - 1));
    var minutes = date.getMinutes() + "";
    if (minutes.length == 1) {
      minutes = "0" + date.getMinutes();
    }
    let icon;
    let marker;
    let popup;

    if (!this.dataManagementService.pointInterests) {
      this.dataManagementService.getAllPointInterests().subscribe(pointInterests => {
        this.dataManagementService.pointInterests = pointInterests;
        realTimeRecord.relativePosition = this.dataManagementService.getRelativePosition(realTimeRecord.coordinate.lat, realTimeRecord.coordinate.lng);
      })
    } else {
      realTimeRecord.relativePosition = this.dataManagementService.getRelativePosition(realTimeRecord.coordinate.lat, realTimeRecord.coordinate.lng);
    }
    if (realTimeRecord.type === "AA") {
      angle = realTimeRecord.rotationAngle * 8;
    }
    if (this.oldRealTimeRecords && realTimeRecord.type === "GPRMC") {
      let oldRealTimeRecord = this.getOldRealTimeRecord(realTimeRecord.idRealTimeRecord);
      if (oldRealTimeRecord) {
        if (!this.compareTwoCoordinate(oldRealTimeRecord.coordinate, realTimeRecord.coordinate)) {
          angle = Math.atan2(realTimeRecord.coordinate.lng - oldRealTimeRecord.coordinate.lng, realTimeRecord.coordinate.lat - oldRealTimeRecord.coordinate.lat) * 180 / Math.PI;
          this.angles.set(realTimeRecord.idRealTimeRecord, angle);
        } else {
          angle = this.angles.get(realTimeRecord.idRealTimeRecord);
        }
      }
    }
    popup = '<b>Chauffeur:</b> ' + this.dataManagementService.getDriverName(realTimeRecord.vehicule.driver) +
      //   '<br><b>Matricule:</b> ' + realTimeRecord.vehicule.matricule +
      '<br><b>Mark:</b> ' + realTimeRecord.vehicule.mark +
      // '<br><b>Lat,Lng:</b><i> [' + this.PipeLngLat(realTimeRecord.coordinate.lat) + ',' + this.PipeLngLat(realTimeRecord.coordinate.lng) + ']</i><br><b>Vitesse :</b>' + realTimeRecord.speed +
      //   "<br><b>date et l'heure:</b> " + (date.getMonth() + 1) + '/' + date.getDate() + '/' + date.getFullYear() + ' ' + date.getHours() + ':' + minutes +
      "<br><b>Signal GSM:</b> " + realTimeRecord.signal +
      " <i class='fa fa-wifi' aria-hidden='true'></i><br><b>Sat en vue:</b> " + realTimeRecord.satInView + " <i class='fa fa-globe' aria-hidden='true'></i>";
    this.selectedDevice = this.vehicule.device.idDevice;
    this.realtimecoord = realTimeRecord.coordinate;
    this.matricule = realTimeRecord.vehicule.matricule;
    this.latpoi = this.PipeLngLat(realTimeRecord.coordinate.lat);
    this.lngpoi = this.PipeLngLat(realTimeRecord.coordinate.lng);
    this.vitesse = realTimeRecord.speed;
    this.dataManagementService.inverseGeoconding(this.latpoi, this.lngpoi, 17).subscribe(res => {
      this.addresspoi = res.display_name;
    }, (err) => {});



    marker = new Marker(realTimeRecord.coordinate, {
      rotationAngle: angle
    });

    if (realTimeRecord.realTimeRecordStatus == 'VALID' && realTimeRecord.speed == 0 && realTimeRecord.ignition == true) {
      icon = new Icon({
        iconUrl: imagescar + "c4x" + Math.abs(Math.round(angle / 45) * 45) + ".png",
        iconAnchor: [-2, 30],
        popupAnchor: [10, -10],
        iconSize:     [15, 60], 
      });
      this.pathDrawn = true;
      this.imageUri = imagescar + "c4x" + Math.abs(Math.round(angle / 45) * 45) + ".png";
    }
    if (realTimeRecord.realTimeRecordStatus == 'VALID' && realTimeRecord.speed > 0) {
      icon = new Icon({
        iconUrl: imagescar + "c1x" + Math.abs(Math.round(angle / 45) * 45) + ".png",
        iconAnchor: [-2, 30],
        popupAnchor: [10, -25],
        iconSize:     [15, 60], 

      });
      this.imageUri = imagescar + "c1x" + Math.abs(Math.round(angle / 45) * 45) + ".png";
    }
    if (realTimeRecord.realTimeRecordStatus == 'VALID' && realTimeRecord.speed == 0 && realTimeRecord.ignition == false) {
      icon = new Icon({
        iconUrl: imagescar + "c2x" + Math.abs(Math.round(angle / 45) * 45) + ".png",
        iconAnchor: [-2, 30],
        popupAnchor: [10, -25],
        iconSize:     [15, 60], 
      });
      this.imageUri = imagescar + "c2x" + Math.abs(Math.round(angle / 45) * 45) + ".png";
    }
    if (realTimeRecord.realTimeRecordStatus == 'NON_VALID' || realTimeRecord.realTimeRecordStatus == 'TECHNICAL_ISSUE') {
      icon = new Icon({
        iconUrl: imagescar + "c3x" + Math.abs(Math.round(angle / 45) * 45) + ".png",
        iconAnchor: [-2, 30],
        popupAnchor: [10, -25],
        iconSize:     [15, 60], 
      });
      this.imageUri = imagescar + "c3x" + Math.abs(Math.round(angle / 45) * 45) + ".png";
    }
    realTimeRecord.icon = this.imageUri;


    icon = new Icon({
      iconUrl: realTimeRecord.icon,
      iconAnchor: [-2, 30],
      popupAnchor: [10, -25],
      iconSize:     [30, 30], 

    });
    if (realTimeRecord.geocoding) {
      popup = popup + '<hr><b>' + realTimeRecord.geocoding + '</b><br>';
    }
    this.geocodingService.inverseGeoconding(realTimeRecord.coordinate.lat, realTimeRecord.coordinate.lng, 18).subscribe(adress => {
      //  popup = popup + '<hr><b>' + adress.display_name + '</b><br>';
      if (realTimeRecord.relativePosition) {
        //popup = popup + '<hr><center><span class="leaflet-pelias-layer-icon-container"><div class="leaflet-pelias-layer-icon leaflet-pelias-layer-icon-point"></div></span><strong>' + realTimeRecord.relativePosition + '</strong></center>';
        this.relativeposition = realTimeRecord.relativePosition;
      } else {
        this.relativeposition = realTimeRecord.geocoding;
      }
      // realTimeRecord.geocoding = this.getGeocoding(adress.address);
      // realTimeRecord.geocodingDetails = adress.display_name;
      //  this.updateSpecificGroups(realTimeRecord);
      let displaycurrentPath = false;
      if (this.currentPathClickedDeviceIds != null && this.currentPathClickedDeviceIds.indexOf(realTimeRecord.idRealTimeRecord) != -1) {
        displaycurrentPath = true;
        this.pathDrawn = true;
      }
      //  this.clearPolylines();
      this.mapService.removeAllRtMarkers();
      this.mapService.updateRtMarkertest(realTimeRecord.coordinate, popup, icon, realTimeRecord.idRealTimeRecord, displaycurrentPath);
      if (realTimeRecord.idRealTimeRecord == this.selectedDevice) {
        if (realTimeRecord.vehicule.driver) {
          this.driverphone = realTimeRecord.vehicule.driver.telephone;
        }
        if (this.pathDrawn == false && realTimeRecord.speed > 0) {
          this.displayCurrentPath(realTimeRecord.idRealTimeRecord);
        }
      }
    }, (err) => {

      let displaycurrentPath = false;
      if (this.currentPathClickedDeviceIds != null && this.currentPathClickedDeviceIds.indexOf(realTimeRecord.idRealTimeRecord) != -1) {
        displaycurrentPath = true;
      }

      // c'est normal => tu test ici si le record = le current !
      if (realTimeRecord.idRealTimeRecord == this.selectedDevice) {
        marker.bindPopup(popup);
        this.mapService.updateRtMarkertest(realTimeRecord.coordinate, popup, icon, realTimeRecord.idRealTimeRecord, displaycurrentPath);
      }
    });
  }

  //
  PipeLngLat(value: any) {
    return this.pipe.transform(value, '2.2-6');
  }

  //update du groupe
  updateSpecificGroups(realTimeRecord: RealTimeRecord) {
    if (this.groups)
      this.groups.forEach(group => {
        if (group)
          group.vehicules.forEach(vehicule => {
            if (vehicule.idDevice == realTimeRecord.idRealTimeRecord)
              vehicule.realTimeRecord = realTimeRecord;
          })
      })
  }

  //alert lancement Moteur
  AlertLancementMoteur(deviceId) {
    this.show = !this.show;
    let alert = this.alertCtrl.create({
      title: 'Attention',
      message: 'Êtes-vous sûr ?',
      buttons: [{
          text: 'Cancel',
          role: 'cancel',
          handler: () => {

          }
        },
        {
          text: 'Start',
          handler: () => {
            this.startEngine(deviceId);
          }
        }
      ]
    });
    alert.present();
  }

  //start moteur
  startEngine(deviceId) {
    this.realTimeService.startEngine(deviceId).subscribe(cmdSended => {
      alert("commande Lancement moteur envoyé !");
    });
  }

  //alert arret moteur
  AlertArretMoteur(deviceId) {
    let alert = this.alertCtrl.create({
      title: 'Attention',
      message: 'Êtes-vous sûr ?',
      buttons: [{
          text: 'Cancel',
          role: 'cancel',
          handler: () => {}
        },
        {
          text: 'Arrêt',
          handler: () => {
            this.stopEngine(deviceId);
          }
        }
      ]
    });
    alert.present();
  }

  //stop moteur
  stopEngine(deviceId) {
    this.realTimeService.stopEngine(deviceId).subscribe(cmdSended => {
      alert("commande Arrêt moteur envoyé !");
    });
  }

  //open result "historique des trajets"
  openResultsModal() {
    this.mapService.removePolylinesFromMap();
    this.mapService.removeMarkersFromMap();
    let pathsListModal = this.modalCtrl.create(PathsList, {
      paths: this.paths
    });
    if (this.paths == null) {
      let alert = this.alertCtrl.create({
        title: 'Historique',
        subTitle: 'Aucune informations',
        buttons: ['Ok']
      });
      alert.present();
    } else {
      pathsListModal.present();
      pathsListModal.onDidDismiss(pathClicked => {
        if (pathClicked == null) {
          this.openFormModal();

        } else if (pathClicked == 1) {

        } else {
          this.drawPath(pathClicked);
        }



        this.resultopened = true;
        this.openStopResult = false;
        this.openMileageResult = false;
      });
    }
  }

  // desiner le trajet
  drawPath(path: any) {

    this.mapService.removePolylinesFromMap();
    this.mapService.removeMarkersFromMap();
    this.historicalService.getPathDetails(path.deviceId, {
      startDate: path.beginPathTime,
      endDate: path.endPathTime
    }).subscribe(points => {
      let stopMarkers: Marker[] = [];
      points.stops.forEach(stop => {
        let popup = "<img src='" + imagesDir + "stop_smal.png" + "'/> Durée d'arrêt : " + stop.stopDurationStr;
        let stopMarker = L.marker({
          lat: stop.stopLatitude,
          lng: stop.stopLongitude
        });
        stopMarker.addTo(this.mapService.map);
        stopMarker.on('click', () => {
          this.mapService.map.setView({
            lat: stop.stopLatitude,
            lng: stop.stopLongitude
          }, 14);
        });
        this.dataManagementService.inverseGeoconding(stop.stopLatitude, stop.stopLongitude, 18).subscribe(adress => {
          let truncatedDisplayName = adress.display_name;
          let countCommas = 0;
          for (var i = 0, len = adress.display_name.length; i < len; i++) {
            if (adress.display_name[i] == ',') {
              countCommas = countCommas + 1;
            } else if (countCommas >= 5) {
              truncatedDisplayName = adress.display_name.substring(0, i - 1);
              break;
            }
          }
          popup = popup + '<br><hr><b>' + truncatedDisplayName + '</b>';
          stopMarker.bindPopup(popup);
        });
        stopMarker.setIcon(new Icon({
          iconUrl: imagesDir + "stop_smal.png",
          iconAnchor: [-2, 30],
          popupAnchor: [10, -25]
        }));
        this.mapService.addMarker(stopMarker);
      });
      let polyline = L.polyline(points.coordinates, {
        color: '#0031D9',
        weight: 3
      });
      points.coordinates.forEach((coordinate, i) => {
        let marker = L.marker(coordinate);
        if (i != 0 && i != points.coordinates.length - 1) {
          marker.setIcon(new Icon({
            iconUrl: imagesDir + "green-point.png",
            iconAnchor: [2, 2]
          }));
          var pointDatePipe = new DatePipe(coordinate.date);
          let popup = '<b>Heure:</b> ' + pointDatePipe.transform(coordinate.date, 'dd/MM/yyyy HH:mm:ss') + '<b><br>vitesse:</b> ' + coordinate.speed + ' Km/h';
          marker.bindPopup(popup);
          marker.on('click', () => {
            this.mapService.map.setView(coordinate, 17);
          });
          this.mapService.addMarker(marker);
        }
      });
      let startMarker = L.marker({
        lat: path.beginPathLatitude,
        lng: path.beginPathLongitude
      });
      let startTime: any = path.beginPathTime;
      var startDatePipe = new DatePipe(startTime);
      startMarker.bindPopup('<b> Lieu de début: </b>' + path.beginPathGeocoding + '<b></i><br>Temps de début du trajet : </b>' + startDatePipe.transform(startTime, 'dd/MM/yyyy HH:mm:ss'));
      startMarker.setIcon(new Icon({
        iconUrl: imagesDir + "startMarker.png",
        iconAnchor: [-2, 30],
        popupAnchor: [10, -25]
      }));
      this.mapService.addMarker(startMarker);
      if (path.endPathLatitude != null && path.endPathLongitude != null && path.endPathTime != null) {
        var endMarker = L.marker({
          lat: path.endPathLatitude,
          lng: path.endPathLongitude
        });
        let endTime: any = path.endPathTime;
        var endDatePipe = new DatePipe(endTime);
        endMarker.bindPopup('<b> Lieu de fin: </b>' + path.endPathGeocoding + '<br><b>Temps de fin du trajet : </b>' + endDatePipe.transform(endTime, 'dd/MM/yyyy HH:mm:ss') +
          '<br><b> Durée du trajet : </b>' + path.pathDurationStr + "<br><b> Durée d'arrêt : </b>" + path.nextStopDurationStr + "<br><b> Vitesse maximum : </b>" +
          path.maxSpeed + " Km/h <br><b> Kilometrage parcouru : </b>" + path.distanceDriven.toFixed(2) + ' Km'
        );
        endMarker.setIcon(new Icon({
          iconUrl: imagesDir + "endMarker.png",
          iconAnchor: [-2, 30],
          popupAnchor: [10, -25]
        }));
        this.mapService.addMarker(endMarker);
      }
      this.mapService.addPolyline(polyline);
      var middle = points.coordinates[Math.round((points.coordinates.length - 1) / 2)];
      this.mapService.map.setView(middle, 12);
    });
    this.modalopen = true;
  }

  // trajet en courant temps reel
  displayCurrentPath(deviceId: number) {
    this.realTimeService.getCurrentPath(deviceId).subscribe(currentPath => {
      if (currentPath != null) {
        let polyline = L.polyline(currentPath.coordinates, {
          color: '#0031D9',
          weight: 3
        });
        currentPath.coordinates.forEach((coordinate, i) => {
          let marker = new Marker(coordinate);
          if (i != 0 && i != currentPath.coordinates.length - 1) {
            marker.setIcon(new Icon({
              iconUrl: imagesDir + "green-point.png",
              iconAnchor: [2, 2]
            }));
            this.mapService.addMarker(marker);
          }
        });
        let startMarker = L.marker({
          lat: currentPath.beginPathLatitude,
          lng: currentPath.beginPathLongitude
        });
        startMarker.setIcon(new Icon({
          iconUrl: imagesDir + "startMarker.png",
          iconAnchor: [-2, 30],
          popupAnchor: [10, -25]
        }));
        startMarker.on('click', () => {
          this.mapService.map.setView({
            lat: currentPath.beginPathLatitude,
            lng: currentPath.beginPathLongitude
          }, 14);
        });
        this.mapService.addMarker(startMarker);
        this.mapService.addPolyline(polyline);
        this.pathDrawn = true;
      }
      this.previousPathdrawn = true;

    }, (err) => {
      this.previousPathdrawn = false;
    });
  }

  //
  processGeocoding(geocoding: string): string {
    if (geocoding != null) {
      let array = geocoding.split(',', 3);
      let smallerGeocoding = '';
      array.forEach(word => {
        smallerGeocoding = smallerGeocoding + ', ' + word;
      });
      smallerGeocoding = smallerGeocoding.slice(2, smallerGeocoding.length);
      return smallerGeocoding;
    } else
      return null;
  }

  //l'appel du chauffeur
  calldriver(number: string) {



    if (number) {
      this.callNumber.callNumber(number, true)
        .then(() => console.log('Launched dialer!'))
        .catch(() => console.log('Error launching dialer'));
    } else {
      let alert = this.alertCtrl.create({
        title: 'Error',
        subTitle: 'Numéro introuvable',
        buttons: ['Ok']
      });
      alert.present();
    }


  }

  //draw all point interests
  drawPointInterests() {
    this.mapService.removeMarkersPoiFromMap();
    this.mapService.removePolygonsPoiFromMap();
    this.mapService.removeCirclesFromMap();
    if (this.poisInMap) {
      this.poisInMap = false;
      return;
    } else {

      if (this.dataManagementService.pointInterests == null) {
        this.dataManagementService.getAllPointInterests().subscribe(pointInterests => {
          this.dataManagementService.pointInterests = pointInterests;
          this.pointInterests = pointInterests;
          pointInterests.forEach(pointInterest => {
            this.drawPointInterest(pointInterest);
          });
        });
      } else {
        this.dataManagementService.pointInterests.forEach(pointInterest => {
          this.drawPointInterest(pointInterest);
        });
      }
      this.poisInMap = true;
    }
  }

  //draw une seule point d'interet
  drawPointInterest(pointInterest: PointInterest) {
    let popup = '<b>Nom : </b> ' + pointInterest.name + '<br><b>Adresse</b>' + pointInterest.address + '';
    let marker = null;
    if (pointInterest.type == "POLYGON") {
      let polygon = new Polygon(pointInterest.decode);
      this.mapService.addPolygonPoi(polygon);
    }
    marker = L.marker(pointInterest.coordinate);
    marker.setIcon(new Icon({
      iconUrl: imagesDir + imagesPOIDir + pointInterest.imageUri,
      iconAnchor: [-2, 10],
      popupAnchor: [10, -25]
    }));
    this.mapService.addMarkerPoi(marker);
    marker.on("click", () => {
      this.mapService.map.setView(pointInterest.coordinate, 17);
    });
    marker.bindPopup(popup);
  }

  // historique des trajet + historique des stops + historique de kilometrage
  openFormModal() {
   
    if (this.selectedDevice) {
    
      let formModal = this.modalCtrl.create(HistoricalForm);
      formModal.present();
      formModal.onDidDismiss(form => {
        
        //historique des trajets
        if (form.endDate != "" && form.startDate != "" && form.list == "trajet") {
          let firstDateTime = new Date(form.startDate);
          let lastDateTime = new Date(form.endDate);
          this.loader = this.loadingCtrl.create({
            content: "Veuillez attendre..."
          });
          this.loader.present();
          if (form.startDate == null && form.endDate == null) {
            this.loader.dismiss();
            let alert = this.alertCtrl.create({
              title: 'Error',
              subTitle: 'Informations erronées',
              buttons: ['Ok']
            });
            alert.present();
          }
          this.allPaths = this.historicalService.getAllPaths(this.selectedDevice, {
            startDate: firstDateTime,
            endDate: new Date(lastDateTime.getTime() + 3600000)
          }).subscribe(paths => {
            if (paths.length) {
              this.pathpoirealtime = false;
              this.pathDrawn = false;
              this.previousPathdrawn = false;
              this.clearPolylines();
              this.history=true;
              this.mapService.removeAllRtMarkers();

              this.paths = paths;
              let lastLat = paths[0].beginPathLatitude;
              let lastlng = paths[0].beginPathLongitude;
              let i = 0;
              this.paths.forEach(path => {
                let startHour: any = path.beginPathTime;
                let startTime: any = startHour + 2 * time_difference;
                path.displayBeginPathTime = startTime;
                if (path.endPathTime != null) {
                  let endHour: any = path.endPathTime;
                  let endTime: any = endHour + 2 * time_difference;
                  path.displayEndPathTime = endTime;
                }
                path.beginPathGeocodingDetails = this.dataManagementService.getRelativePosition(path.beginPathLatitude, path.beginPathLongitude);
                path.beginPathGeocoding = path.beginPathGeocodingDetails;
                if (path.beginPathGeocodingDetails == null) {
                  this.geocodingService.inverseGeoconding(path.beginPathLatitude, path.beginPathLongitude, 17).subscribe(adress => {
                    path.beginPathGeocodingDetails = adress.display_name;
                    if (adress.address.road != null && adress.address.neighbsourhood != null && adress.address.city != null) {
                      path.beginPathGeocoding = adress.address.road + ' ' + adress.address.neighbourhood + '(' + adress.address.city + ')';
                      path.beginPathGeocodingDetails = adress.address.road + ' ' + adress.address.neighbourhood + '(' + adress.address.city + ')';
                    } else {
                      let truncatedDisplayName = adress.display_name;
                      let countCommas = 0;
                      for (var i = 0, len = adress.display_name.length; i < len; i++) {
                        if (adress.display_name[i] == ',') {
                          countCommas = countCommas + 1;
                        } else if (countCommas >= 5) {
                          truncatedDisplayName = adress.display_name.substring(0, i - 1);
                          path.beginPathGeocoding = truncatedDisplayName;
                          path.beginPathGeocodingDetails = path.beginPathGeocoding;
                          break;
                        }
                      }
                    }
                  }, (err) => {
                    path.beginPathGeocodingDetails = null;
                    path.beginPathGeocoding = null;
                  });
                }
                path.endPathGeocodingDetails = this.dataManagementService.getRelativePosition(path.endPathLatitude, path.endPathLongitude);
                path.endPathGeocoding = path.endPathGeocodingDetails;
                if (path.endPathGeocodingDetails == null) {
                  this.geocodingService.inverseGeoconding(path.endPathLatitude, path.endPathLongitude, 17).subscribe(adress => {
                    path.endPathGeocodingDetails = adress.display_name;
                    if (adress.address.road != null && adress.address.neighbsourhood != null && adress.address.city != null) {
                      path.endPathGeocoding = adress.address.road + ' ' + adress.address.neighbourhood + '(' + adress.address.city + ')';
                      path.endPathGeocodingDetails = path.endPathGeocoding;
                    } else {
                      let truncatedDisplayName = adress.display_name;
                      let countCommas = 0;
                      for (var i = 0, len = adress.display_name.length; i < len; i++) {
                        if (adress.display_name[i] == ',') {
                          countCommas = countCommas + 1;
                        } else if (countCommas >= 5) {
                          truncatedDisplayName = adress.display_name.substring(0, i - 1);
                          path.endPathGeocoding = truncatedDisplayName;
                          path.endPathGeocodingDetails = path.endPathGeocoding;
                          break;
                        }
                      }
                    }
                  }, (err) => {
                    path.endPathGeocodingDetails = null;
                    path.endPathGeocoding = null;
                  });
                }
                i++;
                lastLat = path.beginPathLatitude;
                lastlng = path.beginPathLongitude;
              });

            }
            this.loader.dismiss();
            this.openResultsModal();
          }, (err) => {});
        }
        //historique des arret
        if (form.endDate != "" && form.startDate != "" && form.list == "arret") {
          let firstDateTime = new Date(form.startDate);
          let lastDateTime = new Date(form.endDate);
          this.loader = this.loadingCtrl.create({
            content: "Veuillez attendre..."
          });
          this.loader.present();
          if (form.startDate == null && form.endDate == null) {
            this.loader.dismiss();
            let alert = this.alertCtrl.create({
              title: 'Error',
              subTitle: 'Informations erronées',
              buttons: ['Ok']
            });
            alert.present();
          }
          this.allPaths = this.historicalService.getAllStops(this.selectedDevice, {
            startDate: firstDateTime,
            endDate: new Date(lastDateTime.getTime() + 3600000)
          }).subscribe(stops => {
            if (stops && stops.length) {
              this.pathpoirealtime = false;
              this.pathDrawn = false;
              this.previousPathdrawn = false;
              this.clearPolylines();
              this.history=true;
              this.mapService.removeAllRtMarkers();

              this.stops = stops;
              this.stops.forEach(stop => {
                stop.geocodingDetails = this.dataManagementService.getRelativePosition(stop.stopLatitude, stop.stopLongitude);
                stop.geocoding = stop.geocodingDetails;
                stop.beginStopTime = stop.beginStopTime + 2 * time_difference;
                if (stop.geocodingDetails == null) {
                  this.geocodingService.inverseGeoconding(stop.stopLatitude, stop.stopLongitude, 17).subscribe(adress => {
                    stop.geocodingDetails = adress.display_name;
                    if (adress.address.road != null && adress.address.neighbourhood != null && adress.address.city != null) {
                      stop.geocoding = adress.address.road + ' ' + adress.address.neighbourhood + '(' + adress.address.city + ')';
                      stop.geocodingDetails = adress.address.road + ' ' + adress.address.neighbourhood + '(' + adress.address.city + ')';
                    } else {
                      let truncatedDisplayName = adress.display_name;
                      let countCommas = 0;
                      for (var i = 0, len = adress.display_name.length; i < len; i++) {
                        if (adress.display_name[i] == ',') {
                          countCommas = countCommas + 1;
                        } else if (countCommas >= 5) {
                          truncatedDisplayName = adress.display_name.substring(0, i - 1);
                          stop.geocoding = truncatedDisplayName;
                          stop.geocodingDetails = stop.geocoding;
                          break;
                        }
                      }
                    }
                  }, (err) => {
                    stop.geocodingDetails = null;
                    stop.geocoding = null;
                  });
                }
              });
            }
            this.loader.dismiss();
            this.openStopsResultsModal();
          }, (err) => {});
        }

        //historique kilometrage
        if (form.endDate != "" && form.startDate != "" && form.list == "kilo") {
          let firstDateTime = new Date(form.startDate);
          let startHour: any = new Date(form.startDate);
          let lastDateTime = new Date(form.endDate);
          this.loader = this.loadingCtrl.create({
            content: "Veuillez attendre..."
          });
          this.loader.present();
          if (form.startDate == null && form.endDate == null) {
            this.loader.dismiss();
            let alert = this.alertCtrl.create({
              title: 'Error',
              subTitle: 'Informations erronées',
              buttons: ['Ok']
            });
            alert.present();
          }
          this.AllPaths = this.historicalService.getAllMileages(this.selectedDevice, {
            startDate: startHour - (24 * 60 * 60 * 1000),
            endDate: new Date(lastDateTime.getTime() + 3600000)
          }).subscribe(mileages => {
            if(this.mileages){
              this.pathpoirealtime = false;
              this.pathDrawn = false;
              this.previousPathdrawn = false;
              this.clearPolylines();
            this.mapService.removeAllRtMarkers();
            this.history=true;
            }
            this.mileages = mileages;
            this.mileages.forEach(mileage => {
              let startHour: any = mileage.startHour;
              mileage.startHour = startHour + (60 * 60 * 1000);
            });
            this.totalMileage = this.mileages[this.mileages.length - 1];
            this.mileages.splice(this.mileages.length - 1);
            this.loader.dismiss();
            this.openMileageResultModal();
          }, (err) => {});
        }
      });
    } else {
      let alert = this.alertCtrl.create({
        title: 'Historique',
        subTitle: 'Veuillez choisir un véhicule ',
        buttons: ['Ok']
      });
      alert.present();
    }
  }

  //draw arret
  drawStop(stop: Stop) {
    if (this.stopClicked == null || this.stopClicked.beginStopTime != stop.beginStopTime) {
      this.stopClicked = new Stop();
      this.stopClicked.beginStopTime = stop.beginStopTime;
      this.stopClicked.endStopTime = stop.endStopTime;
    }
    this.mapService.removeMarkersFromMap();
    let marker = L.marker({
      lat: stop.stopLatitude,
      lng: stop.stopLongitude
    });
    let date = new Date(stop.beginStopTime);
    let beginStopTime: any = stop.beginStopTime;
    var pointDatePipe = new DatePipe(beginStopTime);
    let popup = "<b>Date debut d'arrêt: </b>" + pointDatePipe.transform(stop.beginStopTime, 'dd/MM/yyyy HH:mm:ss') + "<br><b>Durée d'arrêt: </b>" + stop.stopDurationStr + "<br><hr><b>" + stop.geocodingDetails + "</b>";
    marker.setIcon(new Icon({
      iconUrl: imagesDir + "endMarker.png",
      iconAnchor: [-2, 30],
      popupAnchor: [10, -25]
    }));
    marker.bindPopup(popup);
    marker.on('click', () => {
      this.mapService.map.setView({
        lat: stop.stopLatitude,
        lng: stop.stopLongitude
      }, 17);
    });
    marker.on('add', () => {
      marker.openPopup();
    });
    this.mapService.addMarker(marker);
    this.mapService.map.setView({
      lat: stop.stopLatitude,
      lng: stop.stopLongitude
    }, 15);
  }

  // open modal "historique de kilometrage" result
  openMileageResultModal() {
    this.mapService.removePolylinesFromMap();
    this.mapService.removeMarkersFromMap();
    let mileageListModal = this.modalCtrl.create(MileageComponent, {
      mileages: this.mileages,
      totalMileage: this.totalMileage
    });
    if (this.mileages == null) {
      let alert = this.alertCtrl.create({
        title: 'Historique',
        subTitle: 'Aucune informations',
        buttons: ['Ok']
      });
      alert.present();
    } else {
      mileageListModal.present();
      mileageListModal.onDidDismiss(mileageClicked => {
        if (mileageClicked == null) {
          this.openFormModal();
        } else if (mileageClicked == 1) {

        } else this.drawMileage(mileageClicked);



      });
    }
    this.resultopened = false;
    this.openStopResult = false;
    this.openMileageResult = true;
  }

  // draw Kilometrage
  drawMileage(mileage: any) {
    if (this.mileageClicked == null || this.mileageClicked.startHour != mileage.startHour) {
      this.mileageClicked = new Mileage();
      this.mileageClicked.startHour = mileage.startHour;
      this.mileageClicked.endHour = mileage.endHour;
    }
    let startHour: any = mileage.startHour - (2 * 60 * 60 * 1000);
    this.historicalService.getMileageDetails(this.selectedDevice, {
      startDate: startHour,
      endDate: mileage.endHour
    }).subscribe(mileage => {
      let points = mileage.points;
      let stops = mileage.stops;
      let polyline = L.polyline(points, {
        color: '#0031D9',
        weight: 3
      });
      points.forEach((coordinate, i) => {
        let marker = L.marker(coordinate);
        if (i != 0 && i != points.length - 1) {
          marker.setIcon(new Icon({
            iconUrl: imagesDir + "green-point.png",
            iconAnchor: [2, 2]
          }));
          var pointDatePipe = new DatePipe(coordinate.date);
          let popup = '<b>Heure:</b> ' + pointDatePipe.transform(coordinate.date, 'dd/MM/yyyy HH:mm:ss') + '<b><br>vitesse:</b> ' + coordinate.speed + ' Km/h';
          marker.bindPopup(popup);
          marker.on('click', () => {
            this.mapService.map.setView(coordinate, 17);
          });
          this.mapService.addMarker(marker);
        }
      });
      stops.forEach((stop, i) => {
        let marker = L.marker({
          lat: stop.stopLatitude,
          lng: stop.stopLongitude
        });
        var pointDatePipe = new DatePipe(stop.beginStopTime);
        let popup = "<b>Date debut d'arrêt: </b>" + pointDatePipe.transform(stop.beginStopTime, 'dd/MM/yyyy HH:mm:ss') + "<br><b>Durée d'arrêt: </b>" + stop.stopDurationStr;
        marker.setIcon(new Icon({
          iconUrl: imagesDir + "stop_smal.png",
          iconAnchor: [-2, 30],
          popupAnchor: [10, -25]
        }));
        marker.bindPopup(popup);
        marker.on('click', () => {
          this.mapService.map.setView({
            lat: stop.stopLatitude,
            lng: stop.stopLongitude
          }, 17);
        });
        this.mapService.addMarker(marker);
      });
      this.mapService.addPolyline(polyline);
      var middle = points[Math.round((points.length - 1) / 2)];
      //map center offset(axis y)
      // this.mapService.offsetMap(middle, 12, 0, -120);
    });
  }

  // open resultat des arrets
  openStopsResultsModal() {
    this.mapService.removePolylinesFromMap();
    this.mapService.removeMarkersFromMap();
    let stopListModal = this.modalCtrl.create(StopList, {
      stops: this.stops
    });
    if (this.stops == null) {
      let alert = this.alertCtrl.create({
        title: 'Historique',
        subTitle: 'Aucune informations',
        buttons: ['Ok']
      });
      alert.present();
    } else {
      stopListModal.present();
      stopListModal.onDidDismiss(stopClicked => {
        if (stopClicked == null) {
          this.openFormModal();
        } else if (stopClicked == 1) {

        } else this.drawStop(stopClicked);
      });
    }
    this.resultopened = false;
    this.openStopResult = true;
  }

  isValidPoint(point: {
    lat: number,
    lng: number
  }) {
    if (point && point.lng !== 0 && point.lng !== 0) return true;
    else return false;
  }
  logout(): void {
    localStorage.removeItem('currentuser');
    this._app.getRootNav().setRoot(HomePage);
  }
}



