import {
  Historical
} from './../pages/historical/historical';
import {
  Component,
  ViewChild,
  Inject
} from '@angular/core';
import {
  Platform,
  ToastController,
  AlertController,
  MenuController,
  NavController,
  App,
  ActionSheetController
} from 'ionic-angular';
import {
  StatusBar
} from '@ionic-native/status-bar';
import {
  SplashScreen
} from '@ionic-native/splash-screen';
import {
  HomePage
} from '../pages/home/home';
import {
  Storage
} from '@ionic/storage';
import {
  GestPoi
} from '../pages/gestpoi/gestpoi';
import {
  Vehicules
} from '../pages/datamana/vehicules/vehicules';
import {
  Groupes
} from '../pages/datamana/groupes/groupes';
import {
  DriverComponent
} from '../pages/datamana/chauffeurs/chauffeurs';
                                                                                  


@Component({
  templateUrl: 'app.html'
})

export class MyApp {
  // integration du navController dans app.component.ts
  @ViewChild('mycontent') nav: NavController
  rootPage;
  listhide: boolean = true;
  tempsreelopened: boolean = true;
  alert: any;
  constructor(public platform: Platform, statusBar: StatusBar, private _app: App, public actionSheetCtrl: ActionSheetController, private menuCtrl: MenuController, private alertCtrl: AlertController, private storage: Storage, splashScreen: SplashScreen, public toastCtrl: ToastController) {
    platform.ready().then(() => {
      
      //alert de confirmation de quitter l'application
      platform.registerBackButtonAction(() => {
        if (this.nav.canGoBack()) {
          this.nav.pop();
        } else {
          if (this.alert) {
            this.alert.dismiss();
            this.alert = null;
          } else {
            this.showAlert();
          }
        }
      });
      //check previous signing
      this.checkPreviousAuthorization();
      splashScreen.hide();
      statusBar.show();
      statusBar.styleDefault();
    });
  }
  // fermer menu
  menuclose() {
    this.menuCtrl.close();
    this.listhide = true;
  }
  //list getion de donnée
  hidelist() {
    this.listhide = !this.listhide;
  }
  //navigation to gestion Poi
  GotoGestpoi() {
    this.tempsreelopened = false;
    this.nav.push(GestPoi);
    this.menuclose();
  }
  GotoGestchauf() {
    this.tempsreelopened = false;
    this.nav.push(DriverComponent);
    this.menuclose();
  }
  //navigation to  temps reel et historique
  GotoTempsRealandHistorique() {
    if (!this.tempsreelopened) {
      this._app.getRootNav().setRoot(Historical);
    }
    this.menuclose();
    this.tempsreelopened = true;
  }
  //navigation to gestion de vehicule
  GoToGestVehi() {
    this.tempsreelopened = false;
    this.nav.push(Vehicules);
    this.menuclose();
  }
  //verification d'authentification
  checkPreviousAuthorization(): void {
    this.rootPage = HomePage;

  }
  //alert quitter l'app
  showAlert() {
    this.alert = this.alertCtrl.create({
      title: 'Attention',
      message: 'Voulez vous vraiment quitter ?',
      buttons: [{
          text: 'Cancel',
          role: 'cancel',
          handler: () => {

          }
        },
        {
          text: 'Quitter',
          handler: () => {
            this.platform.exitApp();
          }
        }
      ]
    });
    this.alert.present();
  }
  //navigation groupe"gestion de donnee"
   GoToGestGroup() {
    this.tempsreelopened = false;
    this.nav.push(Groupes);
    this.menuclose();
  }
  //alert deconnexion
  logoutconfirm() {
    this.menuclose();
    let actionSheet = this.actionSheetCtrl.create({
      title: 'Souhaitez-vous vraiment vous déconnecter?',
      buttons: [{
        text: 'Annuler',
        role: 'cancel',
        handler: () => {

        }
      }, {
        text: 'Déconnexion',
        cssClass: 'Logout',
        handler: () => {
          this.logout();
        }
      }]
    });
    actionSheet.present();
  }
  //deconnexion
  logout(): void {
    localStorage.removeItem('currentuser');
    this._app.getRootNav().setRoot(HomePage);
    
  }
  //end
}



