import { User } from './../objects/real-time';
import {Injectable} from '@angular/core';
import 'rxjs/add/operator/map';
import {Observable} from 'rxjs/Observable';
import { Http , Response,Headers, RequestOptions} from '@angular/http';
import {Groupe,Vehicule,VehiculeGetDTO,GroupAddDTO} from '../objects/index';
import {dns} from "./global.config";
import {createAuthorizationHeader} from "../utils/headers";

@Injectable()
export class GroupService {
    currentuser:User;
   constructor(private _http: Http) {
        this.currentuser=JSON.parse(localStorage.getItem('currentuser'));
    }
public toastyAdd:Boolean=false;
public toastyUpdate:Boolean=false;
public groups:Groupe[]=[];
vehiculeLabels:VehiculeGetDTO[]=[];
public bigCurrentPage:number = 1;

      getGroupsByPageAndSize(page:number,size:number): Observable<any> {
                let headers = new Headers({'Accept': 'application/json'});
                headers.append('Authorization', this.currentuser.token);
        return this._http.get(dns +'groupes/list'+'?page='+page+'&size='+size, {headers: headers}).map(res => res.json());
    }
 
      getVehiculesMatricules(): Observable<VehiculeGetDTO[]> {
                let headers = new Headers({'Accept': 'application/json'});
                headers.append('Authorization', this.currentuser.token);
        return this._http.get(dns+'vehicules/minify', {headers: headers}).map(res => res.json());
    }

   getVehiculesGroup(id:number): Observable<VehiculeGetDTO[]> {
            let headers = new Headers({'Accept': 'application/json'});
            headers.append('Authorization', this.currentuser.token);
            return this._http.get(dns+'groupes/vehicules/'+id, {headers: headers}).map(res => res.json());
    }

   addGroup(groupDto: GroupAddDTO): Observable<Groupe> {
                let headers = new Headers({'Content-Type': 'application/json'});
                headers.append('Authorization', this.currentuser.token);
        let options = new RequestOptions({headers: headers});
        let body = JSON.stringify(groupDto);
    return this._http.post(dns+'groupes/',groupDto, options).map(res => res.json());
    }

    deleteGroupById(id:number): Observable<Boolean> {
              let headers = new Headers({'Accept': 'application/json'});
              headers.append('Authorization', this.currentuser.token);
              return this._http.delete(dns+'groupes/'+id, {headers: headers}).map(res => res.json());
    }
     
    public putGroupDTO(groupDto:GroupAddDTO): Observable<Groupe> {
            let headers = new Headers({'Content-Type': 'application/json'});
            headers.append('Authorization', this.currentuser.token);
            let options = new RequestOptions({headers: headers});
        let body = JSON.stringify(groupDto);
    return this._http.put(dns+'groupes/', groupDto, options).map(res => res.json());
                                                                 }                                                                
getGroup(idGroupe: number){
  return this.groups.find(group => group.idGroupe === idGroupe);
}

 getOne(idGroup): Observable<Groupe> {
          let headers = new Headers({'Accept': 'application/json'});
          headers.append('Authorization', this.currentuser.token);
          return this._http.get(dns+'groupes/'+idGroup, {headers: headers}).map(res => res.json());
    }

}




